.searchInputs {
	margin-top: 105px;
	display: flex;
	z-index: 100;
}

.search-input {
	z-index: 100000;
	height: 20px;
}

.searchIcon {
	height: 60px;
	width: 50px;
	background-color: white;
	display: grid;
	place-items: center;
}

.flexible-btn-container {
	background: $clr2;
	z-index: 200;
	width: 412px;
	border-radius: 10px;
	height: 100px;
	padding-block-start: 10px;
	position: absolute;
	left: 0;
	box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
	top: 107%;
	& p {
		margin-inline-start: 40px;
		font-size: 12px;
		color: $clr6;
	}
	& button {
		width: 90%;
		box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
		& ::after {
			// content: url('../svg/btn-arrow.svg');
			position: absolute;
		}
		& span {
			width: 100%;
			display: inline-block;
		}
		align-items: flex-start;
	}
}

.searchIcon svg {
	font-size: 35px;
}

.data-result {
	position: absolute;
	top: 107%;
	left: 0%;
	width: 280px;
	max-height: 200px;
	background-color: white;
	box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
	overflow: hidden;
	overflow-y: auto;
	padding: 0;
	border-radius: 10px;
	&.not-visible {
		visibility: hidden;
	}
}

.data-result::-webkit-scrollbar {
	display: none;
}

.data-result .data-item {
	width: 100%;
	height: 50px;
	display: flex;
	align-items: center;
	color: black;
	padding-inline-start: 10px;
}

.data-item p {
	padding-inline-start: 10px;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
	font-weight: 400;
	font-size: 16px;
	width: 100%;
}

.data-item {
	display: flex;
	justify-content: space-around;
	margin: 0;
	padding: 0;
	& img {
		width: 8%;
		height: 40%;
		background-color: $clr5;
		box-sizing: content-box;
		padding: 3px;
		margin-inline-start: 5px;
		border-radius: 5px;
		border: 1px solid rgba(0, 0, 0, 0.452);
		padding: 2px;
	}
	&:hover {
		background-color: $clr5;
	}
}

@media screen and (min-width: 780px) {
	.flexible-btn-container {
		button {
			position: relative;
			width: 90%;
			transform: translate(5%, 10%);
			margin: 0;
		}
	}
}

@media screen and (max-width: 780px) {
	.flexible-btn-container {
		width: 92vw;
		height: 120px;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		padding-block-end: 40px;
		p {
			margin: 0;
		}
		button {
			width: 85vw;
		}
	}
	.search-input {
		position: fixed;
		z-index: 1000;
		top: 12px;
		left: 7%;
	}
}

@media screen and (max-width: 520px) {
	.flexible-btn-container {
		position: fixed;
		left: -8%;
		top: 60px;
		button {
			width: 75vw;
		}
	}

	.data-result {
		position: fixed;
		max-width: 100%;
		top: 60px;
	}
}
